import axios from "axios";

export function request(config) {
  const instance = axios.create({
    // baseURL: 'http://localhost:80',
    // baseURL: 'http://192.168.43.205:80',
    // baseURL: 'http://192.168.0.171:80',
    timeout: 5000
  });

  return instance(config);
}



