<template>
  <div class="homeTitle">
    <div class="logo"></div>
    <div class="seizeASeat"></div>
    <div class="search">
      <input type="text" />
      <button class="submit"></button>
    </div>
    <div class="information"></div>
  </div>
</template>

<script>
export default {
  name: 'HomeTitle'
}
</script>

<style>
.homeTitle {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.homeTitle > .logo {
  width: 120px;
  height: 60px;
  background-image: url("~assets/img/logo.png");
  background-size: 100%;
  background-repeat: no-repeat;
  cursor: pointer;
}

.homeTitle > .seizeASeat {
  flex: 1;
}

.homeTitle > .search {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 40px;
  padding: 0px 0px 0px 16px;
  border: 1px solid rgb(58, 175, 221);
  border-radius: 50px;
  box-sizing: border-box;
}

.homeTitle > .search > input {
  width: 240 px;
  border: 0px;
  font-size: 16px;
  color: #666;
  background-color: transparent;
}

.homeTitle > .search > button {
  width: 38px;
  height: 38px;
  border: 0px;
  border-radius: 50px;
  background-image: url("~assets/img/search.png");
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: center center;
  background-color: rgb(58, 175, 221);
  cursor: pointer;
  transition: background-color 0.3s;
}

.homeTitle > .search > button:hover {
  background-color: rgb(98, 198, 238);
}

.homeTitle > .information {
  width: 38px;
  height: 38px;
  margin-left: 20px;
  border-radius: 50px;
  background-image: url("~assets/img/information.png");
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: center center;
  background-color: rgb(58, 175, 221);
  cursor: pointer;
  transition: background-color 0.3s;
}

.homeTitle > .information:hover {
  background-color: rgb(98, 198, 238);
}
</style>


