import {
  request
} from './request';

/**
 * 获取用户信息
 * @param {*} params sno, type['simple', 'complete']
 */
export function getUserInfo(params) {
  return request({
    method: 'get',
    url: '/userInformation',
    params
  });
}

/**
 * 设置用户信息
 * @param {*} params sno, age, height, weight, personalProfile, contactInformation
 * @returns 
 */
export function setUserInfo(params) {
  console.log(params);
  return request({
    method: 'POST',
    url: '/userInformation',
    params
  });
}

/**
 * 获取配对列表 
 * @param {*} params (sno)
 */
export function getPairingList(params) {
  return request({
    method: 'get',
    url: '/pairingList',
    params
  });
}

/**
 * 添加配对请求
 * @param {*} params sno, content
 */
export function addPairingRequest(params) {
  return request({
    method: 'post',
    url: '/pairingList',
    params
  });
}

/**
 * 获取好友列表
 * @param {*} params sno
 */
export function getFriendList(params) {
  return request({
    method: 'get',
    url: '/friendList',
    params
  })
}



